<template>
  <div>
    <div class="top-spacer"></div>

    <div class="relative">
      <div class="items">
        <div v-if="show_items">
        <div class="items">
          <div class="item" v-for="item in filtered_items" :key="item.id">
            <router-link :to="'/design/' + item.name">
              <div class="item">
                <p class="item-header center" style="width:100%">{{ item.name }}</p>
                <img :src="'/images/' + item.images[0]">
              </div>
            </router-link>
          </div>
        </div>
        </div>
      </div>
    </div>

    <div class="bottom-spacer top-spacer"></div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data: function() {
     return {
       perpage: 1000,
       pageNumber: 0
     }
   },
  methods: {
    contains_object(obj, list) {
      var i;
      for (i = 0; i < list.length; i++) {
        if (list[i] === obj) {
          return true;
        }
      }
      return false;
    }
  },
  computed: {
    num_pages() {
      let numRecipes = this.filtered_items.length
      let numPages = Math.floor(numRecipes / this.perpage) + 1
      return (this.pageNumber + 1).toString() + " of " + numPages.toString() + " pages"
    },
    show_items() {
      return this.items.length > 0;
    },
    filtered_items() {
      let items = this.$root.$data.items
      let filter_word = this.$root.$data.filter

      if (filter_word != "all") {
        items = this.$root.$data.items.filter(item =>
            this.contains_object(filter_word, item.categories)
        )
      }

      console.log(items);
      return items;
    },
    items() {
      let items = this.filtered_items

      items = items.slice(this.pageNumber*this.perpage,this.pageNumber*this.perpage+this.perpage)
      return items;
    },
    show_photos() {
      return this.$root.$data.show_photos;
    },
    show_art() {
      return this.$root.$data.show_art;
    },
    show_design() {
      return this.$root.$data.show_design;
    }
  }
};
</script>

